.homescreen__title{
  font-size:1.5rem;
  color: #171717;
  margin-bottom: 1rem;
  margin-left:8px;
}

.homescreen__products{
  display:grid;
  grid-template-columns: repeat(4, 1fr);
}

@media (max-width: 1232px) {
  .homescreen__products{
