 .img-wrapper
{
  width: 100%;
  height:100%;
	position: relative;
	overflow: hidden;
  object-fit: contain;
  border-radius: 8px;
  margin-bottom: -30px;
} 

.img-wrapper:before
{
	content: '';
	position: absolute;
	top: 0;
	left: 180%;
	height: 100%;
	width: 100%;
	background: rgba(255,255,255,.3);
	z-index: 1;
	-webkit-transform: skew(45deg);
	        transform: skew(45deg);
	transition: .5s;
}

.img-wrapper:hover:before
{
	left: -180%;
}

.img-wrapper img
{
/* 	height: 400px; */
	/* width: 300px; */
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
	transition: 2s;
}
.img-wrapper:hover img
{
	-webkit-filter: grayscale(0%);
	        filter: grayscale(0%);
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.img-wrapper h2 
{
	background: tomato;
	font-family: Poppins;
	color: #fff;
	text-align: center;
	text-transform: uppercase;
	margin: 0;
	padding: 10px 0;
	position: absolute;
	bottom: 0;
	width: 100%;
	-webkit-transform: perspective(400px) rotateY(90deg);
	        transform: perspective(400px) rotateY(90deg);
	-webkit-transform-origin: right;
	        transform-origin: right;
	transition: 1s;
  font-size: 20px;
  font-weight: 300;
}

.img-wrapper:hover h2
{
	-webkit-transform: perspective(400px) rotateY(0deg);
	        transform: perspective(400px) rotateY(0deg);
}
.img-wrapper p{
  background: tomato;
	font-family: Poppins;
	color: #fff;
	text-align: center;
	text-transform: uppercase;
  margin: 0;
	padding-top:10px 0 ;
  top:0;
	position: absolute;
	bottom: 0;
	width: 100%;
	-webkit-transform: perspective(400px) rotateY(90deg);
	        transform: perspective(400px) rotateY(90deg);
	-webkit-transform-origin: right;
	        transform-origin: right;
	transition: 1s;
  font-size: 20px;
  font-weight: 300;
}
.img-wrapper:hover p{
  -webkit-transform: perspective(400px) rotateY(0deg);
          transform: perspective(400px) rotateY(0deg);

}

.img-wrapper ul
{
	position: absolute;
	top: 0;
	left: 0;
	margin: 0;
	padding: 0;
	list-style: none;
	background: rgba(255,255,255,0);
}

.img-wrapper ul li , .info__price
{
	background: #333;
	height: 40px;
	width: 40px;
	text-align: center;
	line-height: 40px;
	-webkit-transform: perspective(800px) rotateY(90deg);
	        transform: perspective(800px) rotateY(90deg);
	transition: .5s;
	-webkit-transform-origin: left;
	        transform-origin: left;
}

.img-wrapper:hover ul li 
{
	-webkit-transform: perspective(800px) rotateY(0deg);
	        transform: perspective(800px) rotateY(0deg);
}

.img-wrapper:hover ul li:nth-child(1)
{
	transition-delay: .2s;
}

.img-wrapper:hover ul li:nth-child(2)
{
	transition-delay: .6s;
}

.img-wrapper:hover ul li:nth-child(3)
{
	transition-delay: .8s;
}

.img-wrapper:hover ul li:nth-child(4)
{
	transition-delay: 1s;
}

.img-wrapper ul li a
{
	color: tomato;
	background: rgba(255,255,255,0);
}

.img-wrapper ul li i
{
	color: tomato;
	background: rgba(255,255,255,0);
}

.img-wrapper ul li i:hover
{
	color: #fff;
	background: rgba(255,255,255,0);
}

